import React from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import validator from 'validator';
class Signup extends React.Component {
  constructor() {
    super();
    this.state = {
      adminName: '',
      adminEmail: '',
      adminPassword: '',
      adminPicture: ''
    };
  }

  handleSubmit = e => {
    e.preventDefault();
    if (validator.isEmail(this.state.adminEmail)) {
      fetch('/api/v1/admins/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          adminName: this.state.adminName,
          adminEmail: this.state.adminEmail,
          adminPassword: this.state.adminPassword,
          adminPicture: this.state.adminPicture
        })
      })
        .then(res => res.json())
        .then(data => {
          if (data.success) {
            alert('successfully registered');
            this.props.history.push('/admins/signin');
          } else {
            alert('enter valid details');
          }
        });
    } else {
      alert('enter a valid details');
    }
  };

  handleChange = event => {
    let { name, value } = event.target;
    this.setState({ [name]: value });
  };

  render() {
    return (
      <div className="sign-wrapper rsign-wrapper">
        <div className="sign-section">
          <form className="sign-form">
            <label className="sign-label" htmlFor="">
              Choose a admin name
            </label>
            <input
              className="sign-input"
              type="text"
              name="adminName"
              placeholder="e.g: prasanth"
              value={this.state.adminName}
              onChange={this.handleChange}
            />
            <label className="sign-label" htmlFor="">
              Enter a valid email address
            </label>
            <input
              className="sign-input"
              type="email"
              name="adminEmail"
              placeholder="example@gmail.com"
              value={this.state.adminEmail}
              onChange={this.handleChange}
            />
            <label className="sign-label" htmlFor="">
              Password
            </label>
            <input
              className="sign-input"
              type="password"
              name="adminPassword"
              placeholder="e.g: 123@qwerty"
              value={this.state.adminPassword}
              onChange={this.handleChange}
            />
            <label className="sign-label" htmlFor="">
              Add online url of profile picture
            </label>
            <input
              className="sign-input"
              type="text"
              name="adminPicture"
              placeholder="e.g: www.myprofilepic.com/picprasanth"
              value={this.state.adminPicture}
              onChange={this.handleChange}
            />

            <input
              className="sign-btn nav-item-btn"
              type="submit"
              value="SIGN UP"
              onClick={this.handleSubmit}
            />
            <div className="sign-footer">
              <p className="sign-footer-text">Already have an account?</p>
              <NavLink className="sign-footer-link" to="/admins/signin">
                Sign in
              </NavLink>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(Signup);
